'use server'

import { adminDb } from '@/lib/firebase-admin'

interface ConsultationData {
    name: string
    email: string
    phone: string
    service: string
    description?: string
    status: 'pending' | 'completed'
    createdAt: string
}

export async function createConsultation(formData: FormData): Promise<{ success: boolean; id?: string; error?: string }> {
    try {
        const consultation: ConsultationData = {
            name: formData.get('name') as string,
            email: formData.get('email') as string,
            phone: formData.get('phone') as string,
            service: formData.get('service') as string,
            description: formData.get('description') as string || '',
            status: 'pending',
            createdAt: new Date().toISOString()
        }

        if (!consultation.email || !consultation.phone) {
            throw new Error('Faltan campos requeridos')
        }

        const docRef = await adminDb.collection('consultations').add(consultation)
        return { success: true, id: docRef.id }
    } catch (error: any) {
        console.error('Error creating consultation:', error)
        return { success: false, error: error.message || 'Error desconocido' }
    }
}